.intro {
  width: 100%;
  background-color: $color-night-blue;
  min-height: 880px;
  background-image: url("../img/background_desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 -10%;
  margin-top: -112px;

  h1 {
    margin: 0;
  }

  @include retina {
    background-image: url("../img/background_desktop@2x.jpg");
  }

  @include vp-1023 {
    min-height: 656px;
    background-size: cover;
    background-image: url("../img/background_tablet.jpg");

    @include retina {
      background-image: url("../img/background_tablet@2x.jpg");
    }
  }

  @include vp-767 {
    background-image: url("../img/background_mobile.jpg");
    min-height: 607px;
    background-position: bottom;
  }
}

.intro__main-screen {
  width: 51vw;
  padding: 239px 5px;
  margin: 0 auto;
  color: $color-alice-blue;

  h2 {
    font-weight: 700;
    font-size: 4.18vw;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    margin-bottom: 33px;
  }

  p {
    font-family: "PT Mono", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 22px;
    line-height: 155%;
    width: 685px;
    margin-bottom: 59px;
  }

  @include vp-1439 {
    width: 100%;
    padding: 136px 0;

    h2 {
      font-weight: 700;
      font-size: 64px;
      line-height: 94px;
      letter-spacing: 0.07em;
      text-transform: uppercase;
      margin-bottom: 0;
    }

    p {
      width: 100%;
      margin-top: -11px;
      margin-bottom: 73px;
    }
  }

  @include vp-767 {
    padding: 124px 0;

    h2 {
      margin-top: 75px;
      font-size: 36px;
      line-height: 42px;
    }

    p {
      font-size: 14px;
      line-height: 155%;
      margin-top: 7px;
      margin-bottom: 53px;
    }
  }
}

.intro__button {
  cursor: pointer;
  width: 410px;
  border: none;
  min-height: 55px;
  background: $color-deep-sky-blue;
  color: $color-alice-blue;
  border-radius: 3px;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 15px 25px;

  .intro__button-mobile {
    display: none;
  }

  @include vp-1439 {
    margin-left: 45px;
  }

  @include vp-767 {
    width: 100%;
    min-height: 43px;
    margin-left: 0;
    font-size: 14px;
    line-height: 16px;
    padding: 11px 36px;

    .intro__button-desktop {
      display: none;
    }

    .intro__button-mobile {
      display: inline;
    }
  }
}
